/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 3.6rem;
    height: 3.6rem;
    left: 3.2rem;
    top: 3.2rem;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: #000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 2.4rem;
    width: 2.4rem;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #ffffff;
}

/* Sidebar wrapper styles*/

.bm-menu-wrap {
    position: fixed;
    height: 100vh;
    overflow-x: hidden;
    box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
}
.bm-menu {
    background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
    padding: 2.5rem 1.5rem 0 0;
    height: 100vh;
    overflow: hidden !important;
}
.bm-item-list {
    padding: 1.5rem;
}
.menu-item {
    position: relative;
    font-size: 3rem;
    text-decoration: none;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    padding: 1rem;
}
.menu-item::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
    bottom: 0;
    left: 0;
}
.active-item {
    font-weight: bold;
}
.menu-item:hover::before,
.active-item::before {
    visibility: visible;
    height: 0.3rem;
}
@media (max-width: 76.8rem) {
    .bm-burger-button {
        position: fixed;
        width: 2rem;
        height: 2rem;
        left: 2rem;
        top: 2rem;
    }
}

